import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vueHeadful from 'vue-simple-headful';
Vue.config.productionTip = false
Vue.use(vueHeadful, {
  key: 'myMetaTags', // custom key for component option
  component: true
});
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
